@media (min-width: 600px) {
    .navigations {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 6em;
        background-color: #2D333B;
        color: #9DA7B3;
        font-size: 1.5vw;
    }

    .box-nav ul {
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0.5em;
    }

    .box-nav li {
        padding: 2.5%;
    }

    .nav-button {
        background-color: #2D333B;
        color: #9DA7B3;
        text-decoration: none;
        font-weight: bold;
        padding: 0.9vw;
        border-radius: 5px;
        border: 1px solid rgba(128, 128, 128, 0.356);
    }

    .active .nav-button {
        background-color: #22272E;
    }

    .nav-button:hover {
        background-color: #22272E;
        border-radius: 0.3125em;
    }

    input {
        border: none;
        border-radius: 0.3125em;
        font-size: 1vw;
        background-color: #22272E;
        text-align: center;
        height: 3vw;
        width: 20em;
        margin-right: 1em;
        color: #9DA7B3;
        font-family: 'Space Grotesk', sans-serif;
    }

    input:focus {
        outline: none;
        background-color: #1C2128;
    }

    .empty::placeholder {
        color: #006F9E;
        font-size: 1em;
    }

    .search {
        border: none;
        background-color: #1c212800;
        font-size: 1.1vw;
        color: #9DA7B3;
        width: 7em;
        margin-right: 1em;
        height: 2.7em;
        border-radius: 5px;
        border: 1px solid rgba(128, 128, 128, 0.356);
        font-family: 'Space Grotesk', sans-serif;
        font-weight: 700;
    }

    .search:hover {
        background-color: #22272E;
        border-radius: 0.3125em;
    }

    .location {
        background-color: transparent;
        border: 1px solid rgba(128, 128, 128, 0.356);
        border-radius: 5px;
        font-size: 1vw;
        color: #9DA7B3;
        cursor: pointer;
        transition: all 0.2s ease-in-out;
        text-decoration: none;
        padding: 0.9vw;
        margin-right: 1em;
        font-family: 'Space Grotesk', sans-serif;
        font-weight: 700;
    }

    .location:hover {
        background-color: #22272E;
        border-radius: 0.3125em;
    }
}


/*styles for tablets and phones*/

@media (max-width: 400px) {
    .navigations {
        background-color: #2D333B;
        color: #9DA7B3;
    }

    .box-nav ul {
        list-style: none;
        margin: 0;
        padding: 0;
        padding-top: 2vw;
    }

    .box-nav li {
        padding: 0.1em;
    }

    .nav-button {
        background-color: #2D333B;
        color: #9DA7B3;
        border-radius: 5px;
        border: 1px solid rgba(128, 128, 128, 0.356);
        width: 100%;
        font-size: 3vw;
        padding: 1em;
    }

    .active .nav-button {
        background-color: #22272E;
    }

    .nav-button:hover {
        background-color: #22272E;
        border-radius: 0.3125em;
    }


    .box-input {
        padding: 0.25em;
    }

    input {
        width: 70%;
        height: 8vw;
        border: none;
        font-size: 3vw;
        border-radius: 0.3125em;
        background-color: #22272E;
        text-align: center;
        color: #9DA7B3;
        font-family: 'Space Grotesk', sans-serif;
    }

    input:focus {
        outline: none;
        background-color: #1C2128;
    }

    .empty::placeholder {
        color: #006F9E;
    }

    .search {
        width: 25%;
        float: right;
        padding: 0.75em;
        border: none;
        background-color: #1c212800;
        color: #9DA7B3;
        font-size: 3vw;
        border-radius: 5px;
        border: 1px solid rgba(128, 128, 128, 0.356);
        font-family: 'Space Grotesk', sans-serif;
        font-weight: 700;
    }

    .search:hover {
        background-color: #22272E;
        border-radius: 0.3125em;
    }

    .box-location {
        padding: 0.25em;
    }

    .location {
        background-color: transparent;
        border: 1px solid rgba(128, 128, 128, 0.356);
        border-radius: 5px;
        padding: 0.75em;
        width: 100%;
        color: #9DA7B3;
        font-size: 3vw;
        text-decoration: none;
        font-family: 'Space Grotesk', sans-serif;
        font-weight: 700;
        margin-bottom: 2vw;
    }

    .location:hover {
        background-color: #22272E;
        border-radius: 0.3125em;
    }
}
