@media (min-width: 600px) {
    .map {
        height: 30em;
        width: 80%;
        border-radius: 10px;
        margin: 0 auto;
    }

    .content-weather-map img {
        width: 5em;
    }

    .content-weather-map img {
        width: 3em;
    }
}



/*styles for tablets and phones*/

@media (max-width: 600px) {
    .map {
        height: 80vw;
        width: 100%;
    }

    .content-weather-map img {
        width: 5em;
    }

    .content-weather-map img {
        width: 3em;
    }
}
